import { Component, OnInit } from '@angular/core';
import { ValidateService } from '../../services/validate.service';
import { MaterializeAction } from 'angular2-materialize';
import { AuthService } from '../../services/auth.service';
import { Router } from '@angular/router';

import {Subject} from 'rxjs/Subject';
import {debounceTime} from 'rxjs/operator/debounceTime';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  name: String;
  email: String;
  username: String;
  password: String;

  private _success = new Subject<string>();
  private _error = new Subject<string>();

  successMessage: string;
  errorMessage: string;

  constructor(
    private _validateService: ValidateService,
    private _authService: AuthService,
    private router: Router
  ) { }

  ngOnInit() {
    this._success.subscribe((message) => this.successMessage = message);
    debounceTime.call(this._success, 5000).subscribe(() => this.successMessage = null);
    this._error.subscribe((message) => this.errorMessage = message);
    debounceTime.call(this._error, 5000).subscribe(() => this.errorMessage = null);
  }

  onRegisterSubmit() {

    const user = {
      name: this.name,
      username: this.username,
      email: this.email,
      password: this.password
    }

    // Required fields
    if(!this._validateService.validateRegister(user)) {
      console.log('please fill in all fields');
      this._error.next('Please fill in all fields');
      return false;
    }

    if(!this._validateService.validateEmail(user.email)) {
      console.log('please use valid email');
      this._error.next('Please use valid email');
      return false;
    }

    // Register user
    this._authService.registerUser(user)
      .subscribe(res => {
        if (res.success) {
          this._success.next('Successfully registered! Proceed to login');
          setTimeout(this.router.navigate(['/login']), 2000);
        } else {
          this._error.next('An error occurred. Please try again');
          setTimeout(this.router.navigate(['/register']), 2000);
        }
      });
  }

}



