import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { Router } from '@angular/router';

import {Subject} from 'rxjs/Subject';
import {debounceTime} from 'rxjs/operator/debounceTime';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  username: String;
  password: String;

  private _success = new Subject<string>();
  private _error = new Subject<string>();

  successMessage: string;
  errorMessage: string;

  constructor(
    private _authService: AuthService,
    private router: Router
  ) { }

  ngOnInit() {
    this._success.subscribe((message) => this.successMessage = message);
    debounceTime.call(this._success, 2000).subscribe(() => {
      this.successMessage = null;
      this.router.navigate(['dashboard']);
      console.log("Done, routed to dashboard");
    });
    this._error.subscribe((message) => this.errorMessage = message);
    debounceTime.call(this._error, 2000).subscribe(() => {
      this.errorMessage = null;
      this.router.navigate(['login']);
      console.log("Done, routed to login");
    });
  }

  onLoginSubmit() {

    const user = {
      username: this.username,
      password: this.password
    }

    console.log("On login submit");

    this._authService.authenticateUser(user)
      .subscribe(data => {
        console.log("Subscribed");
        console.log(data.success);
        console.log(data.msg);
        if(data.success) {
          this._success.next("Login Successful!");
          this._authService.storeUserData(data.token, data.user);

        } else {
          this._error.next(data.msg);
        }
      });
  }

}



